*/
@font-face {
  font-family: "icons";
  src: url("../fonts/icons/icons.ttf") format("truetype"), url("../fonts/icons/icons.woff") format("woff"), url("../fonts/icons/icons.svg#icons") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
ul.is-style-highlights li:before, .btn-secondary:after,
.wp-block-button.is-style-btn-secondary .wp-block-button__link:after, .btn:after,
.btn-primary:after,
input[type=submit]:after,
.wp-block-button.is-style-btn-primary .wp-block-button__link:after, [class^=icon-], [class*=" icon-"] {
  font-family: "icons" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-download:before {
  content: "";
}

.icon-arrow_left:before {
  content: "";
}

.icon-arrow_right:before {
  content: "";
}

.icon-arrow_back:before {
  content: "";
}

.icon-arrow_forward:before {
  content: "";
}

.icon-cert_left:before {
  content: "";
}

.icon-cert_right:before {
  content: "";
}

.icon-facebook:before {
  content: "";
}

.icon-instagram:before {
  content: "";
}

.icon-linkedin:before {
  content: "";
}

.icon-youtube:before {
  content: "";
}

.icon-linkedin_full:before {
  content: "";
  color: #0a66c2;
}

.icon-pdf:before {
  content: "";
}

.icon-social:before {
  content: "";
}

.icon-technic:before {
  content: "";
}

.icon-quality:before {
  content: "";
}

.icon-figure:before {
  content: "";
}

.icon-map:before {
  content: "";
}

.icon-webdevelopment:before {
  content: "";
}

.icon-newsletter:before {
  content: "";
}

.btn-secondary,
.wp-block-button.is-style-btn-secondary .wp-block-button__link, .btn,
.btn-primary,
input[type=submit],
.wp-block-button.is-style-btn-primary .wp-block-button__link {
  display: inline-block;
  text-decoration: none;
  padding: 0 1em;
  min-height: 40px;
  line-height: 38px;
  transition: all 400ms ease-out;
  font-size: 1rem;
  font-weight: 600;
  text-align: center;
  cursor: pointer;
  border: 1px solid;
  border-radius: 0;
  white-space: nowrap;
}
.btn-secondary:hover,
.wp-block-button.is-style-btn-secondary .wp-block-button__link:hover, .btn:hover,
.btn-primary:hover,
input[type=submit]:hover,
.wp-block-button.is-style-btn-primary .wp-block-button__link:hover {
  text-decoration: none;
}
.btn-small.btn-secondary,
.wp-block-button.is-style-btn-secondary .btn-small.wp-block-button__link, .btn-small.btn,
.btn-small.btn-primary,
input.btn-small[type=submit],
.wp-block-button.is-style-btn-primary .btn-small.wp-block-button__link {
  line-height: 2rem;
  padding: 0 0.7em;
  min-height: 2rem;
  text-transform: none;
}
.btn-secondary:after,
.wp-block-button.is-style-btn-secondary .wp-block-button__link:after, .btn:after,
.btn-primary:after,
input[type=submit]:after,
.wp-block-button.is-style-btn-primary .wp-block-button__link:after {
  content: "";
}

.btn,
.btn-primary,
input[type=submit],
.wp-block-button.is-style-btn-primary .wp-block-button__link {
  color: var(--wp--preset--color--base-bg);
  background: var(--wp--preset--color--primary);
  border-color: var(--wp--preset--color--primary);
}
.btn:hover,
.btn-primary:hover,
input[type=submit]:hover,
.wp-block-button.is-style-btn-primary .wp-block-button__link:hover {
  color: var(--wp--preset--color--base-bg);
}
.btn:active,
.btn-primary:active,
input[type=submit]:active,
.wp-block-button.is-style-btn-primary .wp-block-button__link:active {
  border-color: var(--wp--preset--color--base);
}

.btn-secondary,
.wp-block-button.is-style-btn-secondary .wp-block-button__link {
  color: var(--wp--preset--color--base);
  background: var(--wp--preset--color--base-bg);
  border-color: var(--wp--preset--color--secondary);
}
.btn-secondary:active,
.wp-block-button.is-style-btn-secondary .wp-block-button__link:active {
  border-color: var(--wp--preset--color--primary);
}

.container,
.alignfull:not(.block-header-slider):not(.wp-block-image):not(.wp-block-cover) > *,
.alignwide > *:not(.container-large):not(.wp-block-image):not(.wp-block-cover) {
  padding-right: 20px;
  padding-left: 20px;
  max-width: 1440px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .container,
.alignfull:not(.block-header-slider):not(.wp-block-image):not(.wp-block-cover) > *,
.alignwide > *:not(.container-large):not(.wp-block-image):not(.wp-block-cover) {
    padding-right: 50px;
    padding-left: 50px;
    max-width: 1500px;
  }
}

@media (min-width: 768px) {
  .container-small {
    padding-right: 10%;
    padding-left: 10%;
  }
}

.container-large {
  padding-right: 20px;
  padding-left: 20px;
}
@media (min-width: 768px) {
  .container-large {
    padding-right: 50px;
    padding-left: 50px;
  }
}

.noscroll {
  overflow: hidden;
}

#content {
  min-height: 60vh;
  padding-bottom: 3rem;
}
#content > *:first-child:not(.wp-block-cover.alignfull):not(.wp-block-image.alignfull) {
  margin-top: 3rem;
}

body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  background-color: var(--wp--preset--color--base-bg);
  color: var(--wp--preset--color--base);
  font-weight: 400;
}

h1, h2, h3, h4, h5 {
  font-weight: normal;
  color: var(--wp--preset--color--headline);
}

.editor-post-title__block,
.editor-post-title__input {
  font-size: 20px;
  font-weight: 300;
}

h1 {
  margin: 0.67em 0;
  line-height: 1;
  font-size: 2em;
}

h2:not(.post-title) {
  font-size: 1.5em;
  margin: 1.1em 0 0.52em;
  line-height: 1.2;
  position: relative;
  padding-bottom: 0.3em;
}

.h3,
h3 {
  font-size: 1.35em;
  margin: 0.95em 0 0.48em;
  line-height: 1.2;
}

h4:not(.post-title) {
  font-size: 1.2em;
  margin: 0.95em 0 0.48em;
  line-height: 1.2;
}

h5 {
  font-size: 1.1em;
  margin: 0.95em 0 0.48em;
  line-height: 1.2;
}

a {
  color: var(--wp--preset--color--primary);
  transition: color 250ms linear;
}
a:hover {
  color: var(--wp--preset--color--secondary);
}
a:active {
  color: var(--wp--preset--color--base);
}
a.more-link {
  display: inline-block;
}

figure {
  margin: 0;
}

ul.is-style-highlights {
  list-style-type: none;
  padding: 0;
}
ul.is-style-highlights li {
  position: relative;
  padding-left: 1.2em;
  margin-bottom: 0.4em;
}
ul.is-style-highlights li:before {
  content: "";
  position: absolute;
  top: 0.3em;
  left: 0;
}

.block-teaser .wrapper,
.block-worldwide .wrapper {
  padding-right: 50px;
  padding-left: 50px;
  max-width: 1500px;
}

.splide__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.block-header-slider .splide__slide:not(:first-child) {
  display: none !important;
  visibility: hidden !important;
}

.block-header-slider .title {
  display: block;
  font-size: 1rem;
  line-height: 1;
  padding-bottom: 1rem;
}
@media (min-width: 1200px) {
  .block-header-slider .title {
    font-size: 1.3rem;
  }
}
.block-header-slider h1, .block-header-slider h2, .block-header-slider h3 {
  margin: 0;
  padding: 0;
}
.block-header-slider h1:after, .block-header-slider h2:after, .block-header-slider h3:after {
  display: none;
}
.block-header-slider .headline {
  font-weight: normal;
  font-size: 5vw;
  line-height: 1.1;
  text-shadow: 0 0 1px var(--wp--preset--color--base-bg);
}
@media (min-width: 992px) {
  .block-header-slider .headline {
    font-size: 50px;
  }
}
.block-header-slider span.headline {
  display: block;
}
.block-header-slider img {
  display: block;
  width: 100%;
}
.block-header-slider .btn-primary {
  margin-top: 1.5em;
}
.block-header-slider .wrapper {
  position: relative;
}
.block-header-slider .wrapper .inner {
  position: absolute;
  padding: 1.5em;
  display: flex;
  align-items: center;
  justify-content: center;
}

.block-team:not(.persons-1) {
  display: flex;
  flex-wrap: wrap;
  margin-right: -0.5rem;
  margin-left: -0.5rem;
}
@media (min-width: 768px) {
  .block-team:not(.persons-1) {
    margin-right: -1.5rem;
    margin-left: -1.5rem;
  }
}
.block-team:not(.persons-1) .item {
  padding: 0.5rem;
  width: 50%;
}
@media (min-width: 768px) {
  .block-team:not(.persons-1) .item {
    padding: 1.5rem;
  }
}
@media (min-width: 992px) {
  .block-team:not(.persons-1) .item {
    width: calc(100% / 3);
  }
}
@media (min-width: 1400px) {
  .block-team:not(.persons-1) .item {
    width: 25%;
  }
}
.block-team .name,
.block-team .position {
  display: block;
}

.post-item {
  width: 100%;
  max-width: 450px;
  padding: 0.7rem;
}
@media (min-width: 600px) {
  .post-item {
    width: 50%;
  }
}
@media (min-width: 768px) {
  .post-item {
    width: calc(100% / 3);
  }
}
.post-item .post-title {
  margin: 0 0 1rem;
  color: var(--wp--preset--color--widgets);
}
.post-item .post-content {
  padding: 0.5em;
  background-color: var(--wp--preset--color--widgets-bg);
  text-align: center;
  transition: background-color 300ms ease;
}
@media (min-width: 768px) and (max-width: 991px) {
  .post-item .post-content {
    font-size: 75%;
  }
}

.post-grid {
  margin: 0 -0.7rem;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
@media (min-width: 600px) {
  .post-grid {
    justify-content: flex-start;
  }
}

.similar-post {
  padding-top: 3em;
  padding-bottom: 3em;
  background-color: var(--wp--preset--color--widgets-bg);
  color: var(--wp--preset--color--widgets);
}
.similar-post .post-grid .post-item:nth-child(3) {
  display: none;
  visibility: hidden;
}
@media (min-width: 768px) {
  .similar-post .post-grid .post-item:nth-child(3) {
    display: block;
    visibility: visible;
  }
}